import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// ztree
import ZTree from '@/components/common/ZTree/ZTree';
// table
import VXETable from '@/components/common/Table';
// 
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { STATUS_CODE, TITLE, MSG, BTN_TEXT, CONSTANTS_MATERIAL, VARIABLE, EXPORT_NAME } from '@/utils/constantsFile';
// 
// import moment from 'moment';
import SearchBox from '@/components/common/SearchBox';
// 
import {
  materialsGetListMaterialsQueryParameterAction,
  materialsTypeGetTreeAction,
  materialsGetPageMaterialsByMaterialsTypeIdAction,
  materialsGetPageMaterialsByConditionAction
} from '@/api/material';
export default {
  components: {
    // 
    HeaderTitle,
    // 
    HeadOperationItem,
    // table
    VXETable,
    // 
    ZTree,
    SearchBox
  },
  data() {
    return {
      EXPORT_NAME: EXPORT_NAME,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_MATERIAL: CONSTANTS_MATERIAL,
      // 
      deviceQRcode: '',
      deviceName: '',
      // 
      formUpdate: {},
      // 
      typeOptions: [],
      typeOptionsY: [],
      // 
      groupOptions: [],
      // 
      rulesUpdate: {
        materialsCode: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 20, message: '20' }
        ],
        materialsName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 20, message: '20' }
        ],
        materialsModel: [
          { max: 50, message: '50' }
        ],
        materialsTypeId: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }]
      },
      // 
      HeadTitleUrl: '',
      // table
      HeadTitleName: CONSTANTS_MATERIAL.MATERIAL_LIST,
      // / ids
      searchIdArr: [],
      // table
      deviceChecks: [],
      tableName: 'device',
      // 
      radio: 1,
      // --
      radioType: 1,
      // 
      radioTypeDisabled: [1, 2, 3],
      // tab
      tabPosition: 'group',
      // 
      showModelItem: false,
      // 
      fatherNodeData: [],
      //  0 1
      showPage: 0,
      // 
      modelOptions: [],
      materialsUnitList: [],
      materialsAttributes: [],
      // 
      formParams: {
        model: null
      },
      // 
      formInline: {
        cnName: '',
        shortName: '',
        parameterName: '',
        isWarning: 'null'
      },
      // 
      rules: {
        name: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT, trigger: 'blur' }]
      },
      //  1  2 3
      operaType: 1,
      // label
      formLabelWidth: '150px',
      // label
      formLabelWidthShort: '50px',
      // 
      rightMenu: [],
      // 
      form: {
        name: ''
      },
      // 
      titlename: '',
      dialogVisibleQrCode: false,
      // 
      dialogVisibleUpdate: false,
      // 
      dialogVisible: false,
      // ztree
      ztreeObj: {},
      // ztree
      ztreeSelectedNode: {
        id: '-1',
        pId: '-1'
      },
      // ztree
      nodeData: [],
      // table
      tableLoading: false,
      // table
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 
          left: 'pager_left'
        }
      },
      // table
      tableDatas: [],
      // table
      tableColumns: [],
      // 
      tableColumns_old: [
        {
          type: 'checkbox',
          width: '50',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'materialsCode',
          title: CONSTANTS_MATERIAL.MATERIAL_CODE,
          width: 'auto',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'materialsName',
          title: CONSTANTS_MATERIAL.MATERIAL_NAME,
          width: 'auto',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'materialsModel',
          title: CONSTANTS_MATERIAL.MATERIAL_MODEL,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'materialsAttributesName',
          title: CONSTANTS_MATERIAL.MATERIAL_ATTR,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'materialsTypeName',
          title: CONSTANTS_MATERIAL.MATERIAL_TYPE,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'materialsUnit',
          title: CONSTANTS_MATERIAL.MATERIAL_UNIT,
          width: 'auto',
          align: 'center'
        }
      ],
      // 
      tableColumns_head: [],
      // 
      tableColumns_operation: [],
      // 
      extendPropertyVoList: [],
      // 
      searchData: [],
      // 
      srearchList: [],
      // 
      isSearchBoxShow: false,
      // 
      dialogVisibleCustomColumn: false,
      isImportDialogShow: false
    };
  },
  computed: {},
  created() {
    // 
    this.GetListQueryParameter();
    // 
    this.getTree();
    // 
    this.setTableColume();
    // 
    this.findList();
  },
  watch: {
  },
  methods: {
    hideMaterial() {
      this.$emit('hideMaterial');
    },
    /**
     * @description: ztree
     * @param event 
     * @param nodes 
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      // nameid
      this.searchIdArr = [];
      var nameUrl = [];
      nameUrl.push(nodes.name);
      var node = nodes.getParentNode();
      while (node) {
        nameUrl.push(node.name);
        node = node.getParentNode();
      }
      this.HeadTitleUrl = nameUrl.reverse().join(' / ');
      this.searchIdArr = [nodes.id];

      // 
      this.ztreeSelectedNode = nodes;
      // ztree
      this.ztreeObj = obj;
      // 
      this.tablePage.currentPage = 1;
      // 
      this.findList();
    },
    /**
     * @description: 
     * @param row 
     * @return: 
     * @author: ZJM
     */
    getChildren(ids, treeNode) {
      ids.push(treeNode.id);
      if (treeNode.isParent) {
        for (var obj in treeNode.children) {
          this.getChildren(ids, treeNode.children[obj]);
        }
      }
      return ids;
    },
    /**
     * @description: ztree
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // ztree
      this.ztreeObj = obj;
      if (this.ztreeSelectedNode.id && this.ztreeSelectedNode.id !== '-1') {
        var nodes = this.ztreeObj.getNodesByParam(
          'id',
          this.ztreeSelectedNode.id,
          null
        );
        this.ztreeSelectedNode = nodes[0];
        this.ztreeObj.selectNode(nodes[0]);
        this.ztreeObj.expandNode(nodes[0], true);
      }
      this.findList();
    },
    /**
     * @description: table
     * @param currentPage 
     * @param pageSize 
     * @return: 
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // table
      this.tablePage.currentPage = currentPage;
      // table
      this.tablePage.pageSize = pageSize;
      // 
      this.findList();
    },
    /**
     * @description: 
     * @param val
     * @return: 
     * @author: ZJM
     */
    async findList() {
      try {
        // table
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        const paramMore = {};
        if (this.isSearchBoxShow === false) {
          if (this.searchIdArr.length > 0) {
            params.materialsTypeId = this.searchIdArr[0];
          } else {
            params.materialsTypeId = -1;
          }
          const res = await materialsGetPageMaterialsByMaterialsTypeIdAction(params);
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 
            this.tableDatas = res.rows;
            this.tablePage.total = res.total;
            this.tableLoading = false;
          } else {
            // 
            showWarningMessage(res.message);
          }
        } else {
          paramMore.conditionJson1 = this.searchData.conditionArr1;
          paramMore.operator = this.searchData.groupOperator;
          paramMore.conditionJson2 = this.searchData.conditionArr2;
          const res = await materialsGetPageMaterialsByConditionAction(params, paramMore);
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 
            this.tableDatas = res.rows;
            this.tablePage.total = res.total;
            this.tableLoading = false;
          } else {
            // 
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    async getTree() {
      // 
      try {
        const res = await materialsTypeGetTreeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (res.rows) {
            this.nodeData = res.rows;
          } else {
            this.nodeData = [];
          }
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param name name
     * @return:
     * @author: ZJM
     */
    async togglePage(name) {
      // 
      if (name === 'addDevice') {
        // 
        this.showPage = 1;
      }
    },
    print(val) {
      this.$print(this.$refs.printQr);
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    groupConfig() {
      this.showPage = 3;
    },
    /**
     * @description: 
     * @return: void
     * @author: ZJM
     */
    async setTableColume() {
      // try {
      //   const res = await extendPropertyGetListAction(
      //     {},
      //     {
      //       conditionJson1: [
      //         {
      //           column: 'extendType',
      //           conditionJson1: [],
      //           conditionJson2: [],
      //           current: 0,
      //           operator: 1,
      //           operatorGroup: 0,
      //           orderBy: {},
      //           ptype: 'int',
      //           size: 0,
      //           type: 'eq',
      //           value: 1
      //         }
      //       ]
      //     });
      //   if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
      //     this.extendPropertyVoList = res.rows;
      this.tableColumns = [];
      this.tableColumns_head = this.tableColumns_old.slice(0, this.tableColumns_old.length - 1);
      this.tableColumns_operation = this.tableColumns_old[this.tableColumns_old.length - 1];
      this.tableColumns = this.tableColumns.concat(this.tableColumns_head);
      // if (this.extendPropertyVoList.length > 0) {
      //   const arr = this.extendPropertyVoList;
      //   for (var i = 0; i < arr.length; i++) {
      //     this.tableColumns.push({
      //       field: arr[i].propertyCode,
      //       title: arr[i].extendName,
      //       width: '160',
      //       align: 'center',
      //       formatter: this.$options.filters.formatterExtendValue
      //     });
      //   }
      // }
      this.tableColumns = this.tableColumns.concat(this.tableColumns_operation);
      //     // 
      //     for (var x = 0; x < this.tableColumns.length; x++) {
      //       this.tableColumns[x].zIndex = x;
      //       if (this.tableColumns[x].visible === undefined) {
      //         this.tableColumns[x].yVisible = true;
      //       } else {
      //         this.tableColumns[x].yVisible = this.tableColumns[x].visible;
      //       }
      //       if (this.tableColumns[x].width === undefined) {
      //         this.tableColumns[x].yWidth = 'auto';
      //       } else {
      //         this.tableColumns[x].yWidth = this.tableColumns[x].width;
      //       }
      //     }
      //   } else {
      //     // 
      //     showWarningMessage(res.message);
      //   }
      // } catch (error) {
      //   showErrorNotify(TITLE.NOTIFY, error);
      // } finally {
      // }
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    /**
     * @description:
     * @param
     * @return:
     * @author: CH
     */
    async GetListQueryParameter() {
      try {
        const res = await materialsGetListMaterialsQueryParameterAction({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.srearchList = res.rows;
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
    * @description: 
    * @param 
    * @return: void
    * @author: cyb
    */
    showSearchCard() {
      this.isSearchBoxShow = !this.isSearchBoxShow;
      if (!this.isSearchBoxShow) {
        this.onTablePageChange(1, this.tablePage.pageSize);
      }
    },
    refreshTable() {
      this.findList();
    },
    dbclickRows(row) {
      this.$emit('dbclickRows', row);
    }
  }
};
